<template>
  <div>
    <van-sticky>
      <van-nav-bar title="帮助中心" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    
    <div class="userWrap">
    <!-- <van-field label="验证码" placeholder="请输入验证码" v-model="userInfo.pid_member"  /> -->
    <div class="content">
        <div style="font-size:15px;margin:5px 5px;">申诉内容:</div>
        <textarea class="text" v-model="userInfo.content" placeholder="请输入申诉内容"></textarea>
    </div>
       <van-uploader :after-read="upImg" accept="image/*">
              <van-image
                :src="userInfo.img"
                class="userImg"
                round
                v-if="userInfo.img"
              >
              </van-image>
            </van-uploader>
        <div style="margin-left:7%;font-size:15px;">
            上传截图
        </div>
    <!-- <van-cell-group>
    <van-field label="" placeholder="请输入对方账号" v-model="userInfo.pid_member"  />
    <van-field label="转账数量" placeholder="请输入转账数量" v-model="userInfo.pid_member"  />
      </van-cell-group> -->
      <button class="submitBtn" @click="updateUser">提交</button>
    </div> 
  </div>
</template>

<script>
import { userInfo, uploadImg, updateUser,addAppeal } from "@/api/personal";
export default {
  data() {
    return {
      userInfo: {
        img: "",
        content:""
      },
      img: ""
    };
  },
  created() {
  },
  methods: {
    upImg(file) {

      this.image = file.content.substring(file.content.indexOf(",") + 1);
      let data={
        img:this.image
      }
      uploadImg(data).then(resp => {
        this.userInfo.img = resp.img;
      });
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    // 提交
    updateUser() {
      addAppeal(this.userInfo).then(res => {
          if(res.errcode==0){
         setTimeout(() => {
          this.$toast("提交成功!");
        }, 200);
        this.$router.go(-1);
          }else{
          this.$toast(res.msg);
          }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.van-uploader{
    position: relative;
    left: 5%;
}
.cover{
    width: 80px;
    height: 80px;
}
.content{
    margin: 15px 5%;
    width: 90%;
    height: 150px;
    box-shadow: 0 4px 5px 5px solid rgba(255,255,255,1);
    border: 1px solid lightgray;
}
.text{
    border: none;
    background: none;
    font-size: 15px;
    text-indent: 40px;
    height:70%;
    width: 100%;
}

/deep/ .van-field__label{
    width: 120px;
}
.van-button--primary{
     background: white;
     color: #28A6FF;
     border: 1px solid #28A6FF;
     border-radius: 20px;
}
.van-cell {
  padding: 15px 16px 19px;
}
.userImg {
  width: 39px;
  height: 39px;
}
.submitBtn {
  display: block;
  width: 345px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  font-size: 18px;
  border-radius: 20px;
  background: #28A6FF;
  margin: 66px auto;
  border: 0 none;
}
</style>
